.continue-with-google {
  width: 80%;
  max-width: 300px; /* Optional: Set a max-width to prevent the button from getting too wide on larger screens */
  height: 50px;
  line-height: 50px; /* Center the text vertically */
  text-align: center;
  border-radius: 25px; /* Half of the height for rounded corners */
  background-color: #4285f4; /* Google Blue */
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  margin: 0 auto; /* Center the button horizontally */
}

.LoginMessage {
  font-size: 2.5vh;
  margin-bottom: 200px;
  text-align: center;
  color: #8747d4;
}

.LoginMessageHeader {
  font-size: 5vh;
  font-weight: bold;
  margin-bottom: 100px;
  text-align: center;
  color: #8747d4;
}



.GetStartedImagesContainer {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 60%; /* Adjust the height to 60% of the viewport height */
  background-color: #262c3c; /* Add black background color */
}

.GetStartedRow {
  display: flex;
  margin-bottom: 20px; /* Add margin between rows */
  margin-top: 20px;
}

.GetStartedImage {
  flex: 0 0 auto;
  width: 400px; /* Adjust the width as needed */
  height: auto; /* Adjust the height as needed */
  margin-right: 30px;
  border-radius: 50px; /* Add rounded border */
}

.GetStartedButton {
  font-size: 2.5vh;
  border-radius: 999px; /* Use a large value to make it pill-shaped */
  width: 80%;
  background: #8747d4;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
}

.GetStartedLoginText {
  margin-top: 10px;
  font-size: 2vh;
}

.GetStartedHeader {
  font-size: 5vh;
  font-weight: bold;
  text-align: center;
  color: #8747d4;
}

.MessageTopSection {
  height: 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.MessageMiddleSection {
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.MessageBottomSection {
  height: 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.WelcomeMessageContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
}



.WelcomeMessageImagesContainer {
  display: flex;
  height: 60%; /* Adjust the height to 60% of the viewport height */
flex-direction: column;
align-items: center;
justify-content: center;
}

.WelcomeMessageImagesContainerScrollable {
  overflow-y: scroll;
  display: flex;
  height: 60%; /* Adjust the height to 60% of the viewport height */
flex-direction: column;
align-items: center;
justify-content: center;
}

.WelcomeMessageText {
  font-size: 2.5vh;
padding: 50px;
  text-align: center;
  color: #8747d4;
}

.WelcomeMessageFooter {
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
